/***  More than One line hide  ***/
.oneline_ellipse {
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.oneline_ellipse_input .MuiInputBase-input {
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

/***  Custom Tooltips for Table and Information Icon popup***/
[data-title]:hover:after {
  opacity: 1;
  transition: all 0.1s ease 0.5s;
  display: block;
}
[data-title]:after {
  content: attr(data-title);
  background-color: #ffffff;
  color: #292929;
  font-weight: normal;
  font-size: 13px;
  position: absolute;
  padding: 5px 10px;
  left: 10px;
  white-space: normal;
  top: 32px;
  box-shadow: 1px 1px 3px #22222233;
  opacity: 0;
  z-index: 99999;
  display: none;
  height: auto;
}
[data-title] {
  position: relative;
  height: auto;
}
.initialtitle {
  position: initial !important;
}

