.c_tooltip:hover .c_tooltiptext {
  visibility: visible;
  z-index: 999;
  border: 1px solid #ebeced;
}

.c_tooltip {
  position: relative;
  display: inline-block;
}
.c_tooltiptext {
  visibility: hidden;
  min-width: 300px;
  background: #f2f4f5 0% 0%;
  box-shadow: 0px 0px 1px #0000001f;
  color: #292929;
  text-align: center;
  padding: 20px;
  position: absolute;
  z-index: 1;
  top: 110%; /* left: 50%; */
  right: 0;
  margin-left: -60px;
}

